import * as React from "react";
import {
  Container,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Collapse,
  Link
} from "@mui/material";
import { ExpandLess, ExpandMore } from "@mui/icons-material";
import { ClassNames } from "@emotion/react";

export default function Home() {
  const [layoutOpen, setLayoutOpen] = React.useState(true);

  const handleClickLayout = () => {
    console.log("Clicked!")
    setLayoutOpen(!layoutOpen);
  };

  return (
    <Container fixed>
      <h1>This is from the Home page.</h1>
      <List>
        <ListItemButton onClick={handleClickLayout}>
          <ListItemText primary="Layout" />
          {layoutOpen ? <ExpandLess /> : <ExpandMore />}
        </ListItemButton>
         
        <Collapse in={layoutOpen} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItemButton>
              <Link href={`${process.env.PUBLIC_URL}/box-layout`}>Box Layout</Link>
            </ListItemButton>
            <ListItemButton>
              <Link href={`${process.env.PUBLIC_URL}/grid-layout`}>Grid Layout</Link>
            </ListItemButton>
          </List>
        </Collapse>
      </List>
    </Container>
  );
}
