import {Box, Grid, styled, Paper, Container} from "@mui/material";

import ComplexGrid from "./ComplexGrid";

const Item = styled(Paper)(({theme}) => ({
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: "center",
    color: theme.palette.text.secondary,
}));

export default function GridLayout(){
    return(
        <Container fixed>
            <Box sx={{flexGrow: 1}}>
            <Grid container spacing={2}>
                <Grid item xs={6} md={8}>
                    <Item>xs=6 md=8</Item>
                </Grid>
                <Grid item xs={6} md={4}>
                    <Item>xs=6 md=4</Item>
                </Grid>
                <Grid item xs={6} md={4}>
                    <Item>xs=6 md=4</Item>
                </Grid>
                <Grid item xs={6} md={8}>
                    <Item>xs=6 md=8</Item>
                </Grid>
            </Grid>
            <hr/>
            <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 6, md: 8, lg: 12 }}>
                <Grid item xs={12}>Container Size: xs= 4, sm= 6, md= 8, lg= 12;</Grid>
                {Array.from(Array(6)).map((_, index) => (
                <Grid item xs={1} sm={2} md={3} lg={4} key={index}>
                    <Item>xs=1 sm=2 md=3 lg=4</Item>
                </Grid>
                ))}
            </Grid>
            <hr/>
            <Grid container>
                <Grid item xs={12}>This is a complex grid.</Grid>
                <Grid item xs={12}>
                    <ComplexGrid/>
                </Grid>
            </Grid>
        </Box>
        </Container>
    )
}